const TableAllegati =({listnum})=>{

    const [json,setJson]=useState([]);
    const [busy,setBusy]=useState(false);
    const [page,setPage]=useState(0);
    const [filterText,setFilterText]=useState('');
    const [find,setFind]=useState([]);

    useEffect(()=>{
      console.log("search on allegati "+filterText);
      
    },[filterText])
    
    const subHeaderComponentMemo = () => {

      const handleClear = () => {
        setFilterText("");
      };
      const handleChange = (val) => {
        setFilterText(val);
      };

      return (
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <ManageSearchRounded sx={{ color: 'action.active', mr: 1, my: 0.5 }} />
            <TextField fullWidth 
              size="small" 
              variant='outlined' 
              value={filterText} 
              label="Ricerca full-text" 
              onChange={(e)=>handleChange(e.target.value)}/>
            <IconButton alt="clear filter" aria-label="clear filter" aria-valuetext="clear filter" sx={{color:blue[800]}} onClick={()=>handleClear()}>
              <Clear/>
           </IconButton>
        </Box>
        );
    };

     /* filter effect */
    useEffect(()=>{
    //let rex = filterText.trim().replace(" ",".+");
    if(filterText.length>0){
      let regex = new RegExp(filterText.trim().replaceAll(" ",".+"), "i");
      const find = json.filter( singlerow => {
        return Object.values(singlerow).filter( v => {
          if(typeof v === 'string' || v instanceof String)  
            return v.match(regex)!==null;
          return false;
        }).length>0
      }).map( o =>o);
      setFind(find);
    }else{
      setFind(json);
    }
    },[json,filterText]);

    const myfetch = (url,page,arraydest,cb)=>{
        let turl = url;
        if((page!==undefined)&&(page!==0)){
          turl+="&page="+page;
        }
        setBusy(true);
        fetch(turl,{
          method:'GET'
        }).then(resp=>resp.json())
        .then(j=>{
          arraydest.push(j.data);
          if(j.current_page<j.last_page){
            myfetch(url,j.current_page+1,arraydest,cb);
            setPage(j.current_page+1);
          } else {
            setJson(arraydest.flatMap(o=>o));
            setBusy(false);
            cb(j);
          }
        }).catch(err=>{console.log("err")})
      };
      
      const selectedcolumns = ()=>{
        if(typeof listnum === 'number'){
          return definedAllegatiVals[listnum-1].colums;
        }
      }
      const selectedTitle = ()=>{
        if(typeof listnum === 'number'){
          return definedAllegatiVals[listnum-1].title;
        }
      }
      useEffect(()=>{
        if(typeof listnum === 'number'){
          let shl = listnum-1;
          if((shl<definedAllegatiVals.length)&&(shl>-1)){
            
            myfetch(tableurl+encodeURI(definedAllegatiVals[shl].query),0,[],()=>console.log("Tabella allegati "+definedAllegatiVals[shl].title+" finish"));
            return;
          }
        } 
        console.log("Numero lista non valido. Inserisci attributo 'listnum' valido sul componente.");
        
      },[]);

    return (
    <Fragment role="main">
    <DataTable 
      customStyles={tablestyle} 
      highlightOnHover 
      subHeader 
      subHeaderComponent={subHeaderComponentMemo()} 
      persistTableHead 
      theme="ENEA" 
      progressPending={busy} 
      progressComponent={
        <p><CircularProgress/></p>
      } 
      ariaLabel={selectedTitle()} 
      columns={selectedcolumns()} 
      data={find} 
      paginationComponentOptions={{
        rowsPerPageText:"Righe per pagina",
      }}
      pagination />
    </Fragment>
      );
}

export {TableAllegati,definedAllegatiVals};