import { Stack,createTheme, ThemeProvider, Divider } from "@mui/material";
import { Fragment } from "react";
import ReactDOM from 'react-dom/client';
import ENEAlogo from "./ENEAlogo";
import { Button } from '@mui/material';
import { styled } from '@mui/material/styles';
import { blue, purple } from '@mui/material/colors';

import TableGare from "./TableLibraryGare";
import TableEsiti from "./TableLibraryEsiti";
import TableAvvisi from "./TableLibraryAvvisi";

import { definedAllegatiVals, TableAllegati} from "./TableLibraryAllegati";

//const root = ReactDOM.createRoot(document.getElementById("root"));
//root.render(<App />);
/*const theme = createTheme({
    palette: {
      primary: purple,
      secondary: lime,
      contrastThreshold: 4.5,
    },
  });*/

  const ColorButton = styled(Button)(({ theme }) => ({
    color: theme.palette.getContrastText(purple[500]),
    borderRadius:0,
    fontFamily:'Barlow',
    backgroundColor: blue[900],
    '&:hover': {
      backgroundColor: blue[600],
    },
  }));



const ENEATables = ({showlogo,name,head,listnum})=>{
    
    const wl = ()=>{
        if(showlogo==='yes'){
            return(<ENEAlogo/>);
        }
        return (<></>);  
    };

    const selectedTitle = ()=>{
        if(typeof listnum === 'number'){
            return definedAllegatiVals[listnum-1].title;
        }
    }

    if(name==='avvisi'){
        return(
        <Fragment>
        <Stack direction='row' spacing={10} sx={{ alignItems: "center" }}>
            {wl()}
            <h1>Elenco {name}</h1>
         </Stack>
        <TableAvvisi/>
        </Fragment>);
    }

    if(name==='esiti'){
        return(
        <Fragment>
        <Stack direction='row' spacing={10} sx={{ alignItems: "center" }}>
            {wl()}
            <h1>Elenco {name}</h1>
         </Stack>
        <TableEsiti/>
        </Fragment>);
    }

    if(name==="gare"){
        return(<Fragment>
            <Stack direction='row' spacing={10} sx={{ alignItems: "center" }}>
                {wl()}
                <h2>Elenco {name}</h2>
            </Stack>
            <TableGare/>
        </Fragment>)
    }
    if(name==="allegati"){
        return (<Fragment>
            <Stack direction='row' spacing={10} sx={{ alignItems: "center" }}>
                {wl()}
                <h2>Elenco {selectedTitle()} ({listnum})</h2>
            </Stack>
            <TableAllegati listnum={listnum}/>       
        </Fragment>);
    }
    return(
        <Fragment>
            <Stack direction='row' spacing={10} sx={{ alignItems: "center" }}>
                {wl()}
                <h2>Tabella {name} non esiste</h2>
            </Stack>
        </Fragment>
    );
}

export default ENEATables;
